@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
html{
  color: rgb(0, 0, 0);
  background-color: rgb(228, 225, 225);   
  background-attachment: fixed;
  scroll-behavior: smooth;
  font-family: 'Varela Round', sans-serif;
}

body {
  margin: 0px;
  padding: 0px;
}

body::-webkit-scrollbar {
  width: 15px
} 

body::-webkit-scrollbar-thumb {   
  background-color: rgb(0, 0, 0);   
  margin-right: 1px;
}


.portfolio{width:100vw}

.aboutMe{height:auto;margin-left:50px;padding-top:15px;display:grid;grid-template-columns:repeat(2, 1fr)}.aboutMe .descricao{text-align:justify;margin:30px}.aboutMe .social{display:grid;grid-template-columns:repeat(3, 1fr)}.aboutMe .icon{font-size:50px;color:#000}.aboutMe .icon:hover{color:#03eea0}.aboutMe img{margin-left:80px}@media (max-width: 850px){.aboutMe{margin-left:0px;grid-template-columns:1fr}.aboutMe .descricao{margin:10%}.aboutMe img{text-align:center;margin-left:0px}}@media (max-width: 550px){.aboutMe img{width:300px;height:300px}}

.skills{padding-top:15px;color:black;font-size:30px;padding-left:60px;background-color:#03eea0}.skills ul{padding-top:40px;padding-bottom:40px;font-size:20px}.skills .title{padding-top:40px}.skills .icons{display:grid;width:40vw;grid-template-columns:repeat(4, 1fr);font-size:50px;padding:10px;border-radius:20px}.skills .icons .icon{margin:20px}@media (max-width: 650px){.skills{font-size:35px;padding-left:10px}.skills .icons{grid-template-columns:repeat(3, 1fr)}.skills ul{font-size:20px}.skills ul li{margin-top:40px}}@media (max-width: 280px){.skills .icons{grid-template-columns:repeat(2, 1fr)}.skills ul{font-size:15px}.skills ul li{margin-top:20px}}

.projects{height:400px;padding-top:40px;margin-bottom:200px;font-size:35px;padding-left:60px}.projects .title{padding-top:40px}.projects .conteiner{display:grid;grid-template-columns:repeat(2, 1fr);padding-bottom:200px}.projects .nameProjectPreview{width:530px;font-size:20px;background-color:#0f8;padding:10px;margin:10px;margin-bottom:0px !important}.projects .projetoPreview{text-align:center;margin:10px;margin-top:0px !important;margin-bottom:40px;width:550px;height:260px;font-size:20px;color:black;margin-bottom:0}.projects .projetoInfo{font-size:20px;padding-bottom:20px;margin:10px;margin-top:0;width:550px;height:auto;text-align:end}.projects .projetoInfo .categoriaGrid{display:grid;margin:20px;grid-template-columns:repeat(4, 1fr);margin-bottom:40px}.projects .projetoInfo .categoriaGrid .categoria{text-align:center;margin:5px;padding:5px;border-radius:20px;background-color:#0f8}.projects .projetoInfo .categoriaGrid .categoria:hover{background-color:#e9a932}.projects .projetoInfo a{margin-right:20px;text-decoration:none;color:black;border:2px solid #0f8;border-radius:25px;padding:10px}.projects .projetoInfo .bottom{margin-bottom:30px}.projects .projetoInfo a:hover{box-shadow:0 0 0.4em #0f8;color:black}@media (max-width: 900px){.projects .conteiner{margin:0;grid-template-columns:1fr}.projects .projetoPreview{margin:10px;margin-bottom:40px}.projects .projetoPreview .projetoInfo{display:none;margin:0 auto;width:300px;height:200px}.projects .projetoPreview .projetoInfo .categoriaGrid{display:grid;grid-template-columns:repeat(3, 1fr)}.projects .projetoPreview .projetoInfo .categoriaGrid .categoria{margin:5px}.projects .projetoPreview .projetoInfo a{text-decoration:none;color:#000;background-color:rgba(87,0,0,0);border-radius:25px;padding:10px;margin-top:5px}.projects .projetoPreview .projetoInfo .bottom{margin-bottom:5x}}@media (max-width: 650px){.projects{padding-left:0px}.projects .conteiner{margin:0;padding-left:0px;grid-template-columns:1fr}.projects .projetoPreview{background-position:center;width:400px;height:220px;margin:10px;margin-bottom:40px}.projects .nameProjectPreview{width:380px}.projects .projetoInfo{width:400px}.projects .projetoInfo .categoriaGrid{grid-template-columns:repeat(2, 1fr)}}@media (max-width: 400px){.projects{padding-left:0px}.projects .conteiner{margin:0;padding-left:0px;grid-template-columns:1fr}.projects .projetoPreview{background-position:center;width:280px;height:160px;margin:10px;margin-bottom:40px}.projects .nameProjectPreview{width:260px}.projects .projetoInfo{width:280px}.projects .projetoInfo .categoriaGrid{grid-template-columns:repeat(2, 1fr)}}

.menu{padding:10px;font-size:14px;position:fixed}.menu .hidden{text-align:center;width:30px}.menu .hidden hr{display:block;border:0;border-top:5px solid #000}.menu .show{display:grid;grid-template-columns:1fr;width:250px;height:150px;font-size:20px}.menu .show a{background-color:#03eea0;text-decoration:none;color:black;padding:15px;border:4px solid whitesmoke}.menu .show a:hover{background-color:whitesmoke;color:black}@media (max-width: 650px){.menu{width:100vw;font-size:auto}.menu a{background-color:white;border:0.5px solid black;padding-right:10px}}

